@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Rubik:wght@300;500&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;font-family:'DM Sans', sans-serif;vertical-align:baseline;scroll-behavior:smooth}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}a{text-decoration:none}

:root{--color-blue: #1B97FF;--color-black: #333333;--color-gray: #D8D8D8;--color-white: #ffffff}.appear{-webkit-animation-name:appearAnimation;animation-name:appearAnimation;-webkit-animation-duration:.5s;animation-duration:.5s;-webkit-animation-timing-function:ease-in-out;animation-timing-function:ease-in-out}@-webkit-keyframes appearAnimation{0%{opacity:0}100%{opacity:1}}@keyframes appearAnimation{0%{opacity:0}100%{opacity:1}}.appearFromRight{-webkit-animation-name:appearRightAnimation;animation-name:appearRightAnimation;-webkit-animation-duration:.5s;animation-duration:.5s;-webkit-animation-timing-function:ease-in-out;animation-timing-function:ease-in-out}@-webkit-keyframes appearRightAnimation{0%{opacity:0;transform:translateX(24px)}100%{opacity:1;transform:translateY(0)}}@keyframes appearRightAnimation{0%{opacity:0;transform:translateX(24px)}100%{opacity:1;transform:translateY(0)}}.body{background-color:#FAFAFA}.title{font-weight:bolder;color:#333333;color:var(--color-black);font-size:20px;margin-bottom:24px}

.Home{width:100%;display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:space-between;align-items:flex-start}.Home main{width:100%}.Home aside{width:0;height:100vh;background-color:red;transition:all .4s}@media only screen and (min-width: 600px){.Home aside{width:400px}}

.Logo path:not(.Icono){display:none}.Logo path{transition:all .4s}@media only screen and (min-width: 650px){.Logo path:not(.Icono){display:block}}

.iconoHamburguesa{cursor:pointer}.iconoHamburguesa_line{transition:all .4s;stroke:#000000}.iconoHamburguesa:hover .iconoHamburguesa_line{stroke:red}.iconoHamburguesaActive .iconoHamburguesa_line1{stroke:red !important;d:path("M 4 4 L 22 22")}.iconoHamburguesaActive .iconoHamburguesa_line2{stroke:red !important;d:path("M 4 22 L 22 4")}.iconoHamburguesaActive .iconoHamburguesa_line3{opacity:0;stroke:red !important}

.header{display:flex;flex-direction:row;justify-content:space-between;align-items:center;position:-webkit-sticky;position:sticky;top:0;z-index:1;width:100%;height:60px;padding:0 24px;box-sizing:border-box;background-color:white;box-shadow:0px 4px 6px rgba(0,0,0,0.05)}.header__nav{display:none}.header__nav a{text-decoration:none;color:var(--color-black);transition:all .4s;margin-right:18px;display:flex;flex-direction:row;justify-content:center;align-items:center}.header__nav a img{margin-right:10px}.header__nav a:last-child{margin-right:0}.header__nav a:hover{color:var(--color-blue)}.header__nav-selected{font-weight:bolder !important;color:var(--color-blue) !important}.header__user{display:none;flex-direction:row;justify-content:flex-start;align-items:center}.header__user__img{width:40px;height:40px;background-color:red;border-radius:40px;margin-right:12px}.header__user__btn{background-color:var(--color-gray);border:none;display:block;height:40px;padding:0 20px;font-weight:bold;font-size:18px;color:var(--color-white);border-radius:8px;cursor:pointer}.header__responsive{display:flex}@media only screen and (min-width: 700px){.header .iconoHamburguesa{display:none}.header__nav,.header__user{display:flex}.header__responsive{display:none}}

.MenuResponsive{position:fixed;z-index:5;background-color:#00000050;right:0;left:0;bottom:0;top:60px;display:flex;flex-direction:row;justify-content:flex-end}.MenuResponsive__body{width:90vw;height:100vh;background-color:var(--color-white);padding:24px;box-sizing:border-box}.MenuResponsive__body__user{display:flex;flex-direction:row;justify-content:space-evenly;align-items:center;width:100%;margin-bottom:48px}.MenuResponsive__body__user__img{width:60px;height:60px;background-color:red;border-radius:60px;margin-right:12px}.MenuResponsive__body__user__btn{background-color:var(--color-gray);border:none;display:block;height:40px;padding:0 20px;font-weight:bold;font-size:18px;color:var(--color-white);border-radius:8px;cursor:pointer}.MenuResponsive__body__menu{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start}.MenuResponsive__body__menu a{text-decoration:none;color:var(--color-black);transition:all .4s;margin-bottom:22px;font-size:18px;display:flex;flex-direction:row;justify-content:center;align-items:center}.MenuResponsive__body__menu a img{margin-right:10px}.MenuResponsive__body__menu a:last-child{margin-right:0}.MenuResponsive__body__menu a:hover{color:var(--color-blue)}.MenuResponsive__body__menu-selected{font-weight:bolder !important;color:var(--color-blue) !important}

.BannerFindTeam{width:100%;padding:24px;box-sizing:border-box;display:flex;flex-direction:column;justify-content:center;align-items:center}.BannerFindTeam__wp{width:100%;max-width:350px;background-color:var(--color-black);border-radius:10px;padding:48px 24px;padding-bottom:0;box-sizing:border-box;display:flex;flex-direction:column;justify-content:center;align-items:center}.BannerFindTeam__wp__text{text-align:center;color:var(--color-white);margin-bottom:48px}.BannerFindTeam__wp__flowers{display:none}@media only screen and (min-width: 900px){.BannerFindTeam{display:flex;flex-direction:column;justify-content:center;align-items:flex-start}.BannerFindTeam__wp{width:100%;max-width:100%;display:flex;flex-direction:row;justify-content:flex-start;align-items:center;padding:48px;position:relative}.BannerFindTeam__wp__text{margin-bottom:0;text-align:left;max-width:300px}.BannerFindTeam__wp__img{position:absolute;right:0;bottom:0}}@media only screen and (min-width: 1000px){.BannerFindTeam__wp__flowers{display:block;position:absolute;right:35%;bottom:0}}

.Equipos{width:100%;padding:24px;box-sizing:border-box;display:flex;flex-direction:column;justify-content:center;align-items:flex-start}.Equipos__body{width:100%;display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, auto-fit));grid-gap:36px;transition:all .4s;margin:auto}@media only screen and (min-width: 1000px){.Equipos__body{grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));grid-column-gap:36px}}

.Equipo{width:100%;max-width:350px;padding:24px;box-sizing:border-box;background:#FCFCFC;box-shadow:1px 4px 4px rgba(0,0,0,0.08);border-radius:12px;overflow:hidden;margin:auto}.Equipo__body{display:flex;flex-direction:row;justify-content:space-between;align-items:center;margin-bottom:24px}.Equipo__body__img{width:40%;height:100px;display:flex;justify-content:center;align-items:center;padding:12px;box-sizing:border-box;background:#FCFCFC;box-shadow:1px 4px 4px rgba(0,0,0,0.08);border-radius:8px}.Equipo__body__img img{height:100%;padding:12px;box-sizing:border-box}.Equipo__body__header__title{font-family:'Rubik', sans-serif;font-size:medium;color:var(--color-black);font-size:18px;margin-bottom:6px}.Equipo__body__header__ghz{margin:12px 0}.Equipo__body__header__logos{width:100%;display:flex;justify-content:space-between;align-items:center}.Equipo__schedule{margin-bottom:24px}.Equipo__schedule__header{display:flex;flex-direction:row;justify-content:space-between;align-items:center;margin-bottom:12px}.Equipo__schedule__header__title{font-family:'DM Sans', sans-serif;color:var(--color-black);font-weight:bolder}.Equipo__schedule__header__btns{display:flex}.Equipo__schedule__header__btns div{padding:0 12px;box-sizing:border-box;display:flex;justify-content:center;align-items:center;cursor:pointer;opacity:.5;transition:all .4s}.Equipo__schedule__header__btns div:first-child{transform:rotate(180deg)}.Equipo__schedule__header__btns div:hover{opacity:1}.Equipo__schedule__body{width:100%;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.Equipo__schedule__body__date{width:45%}.Equipo__schedule__body__date p{margin-bottom:12px}.Equipo__schedule__body__date__hour{border:solid 1px #D8D8D8;border-radius:8px;background-color:var(--color-white);display:flex;flex-direction:row;align-items:center;justify-content:center;padding:12px 10px}.Equipo__btn button{background:#F0F0F0;border-radius:8px;border:none;width:100%;padding:12px 0;box-sizing:border-box;cursor:pointer;color:var(--color-black);font-weight:500;font-size:18px;transition:all .4s}.Equipo__btn button:hover{background-color:var(--color-blue);color:var(--color-white)}


